/* 1. Global - Set up the things
/* ---------------------------------------------------------- */

/* Import CSS reset and base styles */
@import "global.css";
@import url("//maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

body {
  font-family: "Roboto", sans-serif;
}

:root {
  /* Colours */
  --color-green: #a4d037;
  --color-yellow: #fecd35;
  --color-red: #f05230;
  --color-darkgrey: #55575a;
  --color-midgrey: #738a94;
  --color-lightgrey: #c5d2d9;
  --color-wash: #e5eff5;
  --color-darkmode: #151719;

  /*
    An accent color is also set by Ghost itself in
    Ghost Admin > Settings > Brand

    --ghost-accent-color: {value};

    You can use this variable throughout your styles
     */

  /* Fonts */
  --font-sans-serif: "Roboto", sans-serif;
  --font-serif: "Roboto", sans-serif;
  --font-mono: Menlo, Courier, monospace;
}
/* 2. Layout - Page building blocks
/* ---------------------------------------------------------- */

.viewport {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.site-content {
  flex-grow: 1;
}

/* Full width page blocks */
.outer {
  position: relative;
  padding: 0 4vmin;
  /* background: #0d3b49; */
}

/* Centered content container blocks */
.inner {
  margin: 0 auto;
  max-width: 1200px;
  width: 100%;
}

/* 4. Site Header
/* ---------------------------------------------------------- */

div {
  display: block;
}

.site-header {
  position: relative;
  color: #fff;
  background: var(--ghost-accent-color);
}

.site-header-cover {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.site-header-content {
  position: relative;
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* padding: 6vw 0vw; */
  min-height: 200px;
  max-height: 340px;
  text-align: center;
}

.site-title {
  z-index: 10;
  margin: 0 0 0.15em;
  padding: 0;
}

.site-logo {
  max-height: 55px;
}

.site-header-content p {
  z-index: 10;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.2em;
  opacity: 0.8;
}

@media (max-width: 600px) {
  .site-header-content p {
    max-width: 80vmin;
    font-size: 1.8rem;
  }
}

/* 4.1 Home header
/* ---------------------------------------------------------- */

.site-home-header {
  position: relative;
  z-index: 1000;
  overflow: hidden;
}

.site-header-content {
  /* padding: 18vmin 4vmin; */
  font-size: 2.5rem;
  font-weight: 400;
  color: #713972;
  /* background: #0d3b49; */
}

.site-header-content.large {
  padding-top: 28vmin;
  padding-bottom: 28vmin;
}

/* 5. Site Navigation
/* ---------------------------------------------------------- */

.gh-head {
  padding: 1vmin 4vmin;
  font-size: 1.6rem;
  line-height: 1.3em;
  color: #fff;
  background: var(--ghost-accent-color);
}

.gh-head a {
  color: inherit;
  text-decoration: none;
}

.gh-head-inner {
  display: grid;
  grid-gap: 2.5vmin;
  grid-template-columns: auto auto 1fr;
  grid-auto-flow: row dense;
}

/* Brand
/* ---------------------------------------------------------- */

.gh-head-brand {
  display: flex;
  align-items: center;
  height: 40px;
  max-width: 200px;
  text-align: center;
  word-break: break-all;
}

.gh-head-logo {
  display: block;
  padding: 10px 0;
  font-weight: 700;
  font-size: 2rem;
  line-height: 1.2em;
  letter-spacing: -0.02em;
}

.gh-head-logo img {
  max-height: 26px;
}

/* Primary Navigation
/* ---------------------------------------------------------- */

.gh-head-menu {
  display: flex;
  align-items: center;
  font-weight: 500;
}

.gh-head-menu .nav {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.gh-head-menu .nav li {
  margin: 0 2.5vmin 0 0;
  padding: 0;
}

.gh-head-menu .nav a {
  display: inline-block;
  padding: 5px 0;
  opacity: 0.8;
}

.gh-head-menu .nav a:hover {
  opacity: 1;
}

/* Secondary Navigation
/* ---------------------------------------------------------- */

.gh-head-actions {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
  text-align: right;
}

.gh-head-actions-list {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
}

.gh-head-actions-list a:not([class]) {
  display: inline-block;
  margin: 0 0 0 1.5vmin;
  padding: 5px 0;
}

.gh-social {
  margin: 0 1.5vmin 0 0;
}

.gh-social a {
  opacity: 0.8;
}

.gh-social a + a {
  margin-left: 0.8rem;
}

.gh-social a:hover {
  opacity: 1;
}

.gh-social svg {
  height: 22px;
  width: 22px;
  fill: #fff;
}

.gh-social-facebook svg {
  height: 20px;
  width: 20px;
}

a.gh-head-button {
  display: block;
  padding: 8px 15px;
  color: var(--button-text-color, var(--color-darkgrey));
  font-weight: 500;
  letter-spacing: -0.015em;
  font-size: 1.5rem;
  line-height: 1em;
  background: var(--button-bg-color, #fff);
  border-radius: 30px;
}

/* Mobile Menu Trigger
/* ---------------------------------------------------------- */

.gh-burger {
  position: relative;
  display: none;
  cursor: pointer;
}

.gh-burger-box {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 33px;
  height: 33px;
}

.gh-burger-inner {
  width: 100%;
  height: 100%;
}

.gh-burger-box::before {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  content: "";
  width: 100%;
  height: 1px;
  background: currentcolor;
  transition: transform 300ms cubic-bezier(0.2, 0.6, 0.3, 1),
    width 300ms cubic-bezier(0.2, 0.6, 0.3, 1);
  will-change: transform, width;
}

.gh-burger-inner::before,
.gh-burger-inner::after {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  content: "";
  width: 100%;
  height: 1px;
  background: currentcolor;
  transition: transform 250ms cubic-bezier(0.2, 0.7, 0.3, 1),
    width 250ms cubic-bezier(0.2, 0.7, 0.3, 1);
  will-change: transform, width;
}

.gh-burger-inner::before {
  transform: translatey(-6px);
}
.gh-burger-inner::after {
  transform: translatey(6px);
}

body:not(.gh-head-open) .gh-burger:hover .gh-burger-inner::before {
  transform: translatey(-8px);
}
body:not(.gh-head-open) .gh-burger:hover .gh-burger-inner::after {
  transform: translatey(8px);
}

.gh-head-open .gh-burger-box::before {
  width: 0;
  transform: translatex(19px);
  transition: transform 200ms cubic-bezier(0.2, 0.7, 0.3, 1),
    width 200ms cubic-bezier(0.2, 0.7, 0.3, 1);
}

.gh-head-open .gh-burger-inner::before {
  width: 26px;
  transform: translatex(6px) rotate(135deg);
}

.gh-head-open .gh-burger-inner::after {
  width: 26px;
  transform: translatex(6px) rotate(-135deg);
}

/* Mobile Menu
/* ---------------------------------------------------------- */
/* IDs needed to ensure sufficient specificity */

@media (max-width: 900px) {
  .gh-burger {
    display: inline-block;
  }
  #gh-head {
    transition: all 0.4s ease-out;
    overflow: hidden;
  }
  #gh-head .gh-head-inner {
    height: 100%;
    grid-template-columns: 1fr;
  }
  #gh-head .gh-head-brand {
    position: relative;
    z-index: 10;
    grid-column-start: auto;
    max-width: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    user-select: none;
  }
  .home-template #gh-head.has-cover .gh-head-brand {
    justify-content: flex-end;
  }
  #gh-head .gh-head-menu {
    align-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 0 0 10vh 0;
    font-weight: 300;
    font-size: 3.6rem;
    line-height: 1.1em;
  }
  #gh-head .gh-head-menu .nav li {
    margin: 5px 0;
  }
  #gh-head .gh-head-menu .nav a {
    padding: 8px 0;
  }
  #gh-head .gh-head-menu .nav {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  #gh-head .gh-head-actions {
    padding: 20px 0;
    justify-content: center;
    text-align: left;
  }
  #gh-head .gh-head-actions a {
    margin: 0 10px;
  }
  /* Hide collapsed content */
  #gh-head .gh-head-actions,
  #gh-head .gh-head-menu {
    display: none;
  }
  /* Open the menu */
  .gh-head-open {
    overflow: hidden;
    height: 100vh;
  }
  .gh-head-open #gh-head {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3999999;
    overflow-y: scroll;
  }
  .gh-head-open #gh-head .gh-head-inner {
    grid-template-rows: auto 1fr auto;
  }
  .gh-head-open #gh-head .gh-head-actions,
  .gh-head-open #gh-head .gh-head-menu {
    display: flex;
  }
}

@media (max-width: 600px) {
  #gh-head .gh-head-menu {
    font-size: 6vmin;
  }
}

.home-template .gh-head.has-cover {
  /* position: absolute; */
  top: 0;
  right: 0;
  left: 0;
  z-index: 2000;
  height: 116px;
}

.home-template .gh-head.has-cover {
  background: #3e5866;
}

.home-template.gh-head-open .gh-head {
  background: var(--ghost-accent-color);
}

.home-template .gh-head.has-cover .gh-head-logo {
  display: none;
}
.home-template .gh-head.has-cover .gh-head-menu {
  margin-left: -2.5vmin;
}

/* 6. Post Feed
/* ---------------------------------------------------------- */

.post-feed {
  position: relative;
  display: grid;
  grid-gap: 4vmin;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 4vmin 0;
}

.post-feed.list {
  row-gap: 6vmin;
  max-width: 940px;
  margin: 2vmin auto 0;
}

@media (max-width: 1000px) {
  .post-feed {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 700px) {
  .post-feed {
    grid-template-columns: 1fr;
    grid-gap: 40px;
  }
}

.post-card {
  position: relative;
  flex: 1 1 301px;
  display: flex;
  flex-direction: column;
  min-height: 220px;
  background-size: cover;
  word-break: break-word;
  background-color: #fff;
  /* padding: 15px; */
}

@media (max-width: 700px) {
  .post-card {
    min-height: auto;
  }
}

.post-card-image-link {
  position: relative;
  display: block;
  overflow: hidden;
  /* border-radius: 3px; */
}

.post-card-image {
  width: 100%;
  height: 200px;
  background: var(--color-lightgrey) no-repeat center center;

  object-fit: cover;
}

.post-card-content-link {
  position: relative;
  display: block;
  color: var(--color-darkgrey);
  padding-top: 0.5em;
}

.post-card-content-link:hover {
  text-decoration: none;
}

.post-card-header {
  margin: 20px 0 0;
}

.post-feed .no-image .post-card-content-link {
  padding: 0;
}

.no-image .post-card-header {
  margin-top: 0;
}

.post-card-primary-tag {
  margin: 0 0 0.2em;
  color: var(--ghost-accent-color);
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: 0.2px;
  text-transform: uppercase;
}

.post-card-title {
  margin: 0 0 0.4em;
  font-size: 2.4rem;
  transition: color 0.2s ease-in-out;
}

.no-image .post-card-title {
  margin-top: 0;
}

.has-serif-title .post-card-title {
  font-family: var(--font-serif);
}

.post-card-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 15px 15px;
}

.post-card-excerpt {
  max-width: 56em;
  color: color-mod(var(--color-midgrey) l(-8%));
  font-family: var(--font-serif);
}

.has-sans-body .post-card-excerpt {
  font-family: var(--font-sans-serif);
}

.post-card-excerpt p {
  margin-bottom: 1em;
  display: -webkit-box;
  overflow-y: hidden;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  word-break: break-word;
}

.post-card-meta {
  display: flex;
  align-items: center;
  padding: 0;
}

.author-profile-image,
.avatar-wrapper {
  display: block;
  width: 100%;
  height: 100%;
  background: color-mod(var(--color-lightgrey) l(+10%));
  border-radius: 100%;

  object-fit: cover;
}

.post-card-meta .profile-image-wrapper,
.post-card-meta .avatar-wrapper {
  position: relative;
}

.author-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 0 4px;
  padding: 0;
  list-style: none;
}

.author-list-item {
  position: relative;
  flex-shrink: 0;
  margin: 0;
  padding: 0;
}

.static-avatar {
  display: block;
  overflow: hidden;
  margin: 0 0 0 -6px;
  width: 36px;
  height: 36px;
  border-radius: 100%;
  box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.2);
}

.post-card-byline-content {
  flex: 1 1 50%;
  display: flex;
  flex-direction: column;
  margin: 0 0 0 8px;
  color: color-mod(var(--color-midgrey) l(+10%));
  font-size: 1.4rem;
  line-height: 1.2em;
  font-weight: 400;
}

.post-card-byline-content span {
  margin: 0;
}

.post-card-byline-content a {
  color: color-mod(var(--color-darkgrey) l(+15%));
  font-weight: 600;
}

.post-card-byline-date {
  font-size: 1.3rem;
  line-height: 1.5em;
}

.post-card-byline-date .bull {
  display: inline-block;
  margin: 0 2px;
  opacity: 0.6;
}

.single-author-byline {
  display: flex;
  flex-direction: column;
  margin-left: 5px;
  color: color-mod(var(--color-midgrey) l(-10%));
  font-size: 1.3rem;
  line-height: 1.4em;
  font-weight: 500;
}

.single-author {
  display: flex;
  align-items: center;
}

.single-author .static-avatar {
  margin-left: -2px;
}

.single-author-name {
  display: inline-block;
}

/* Special Styling for home page grid (below):

The first post in the list is styled to be bigger than the others and take over
the full width of the grid to give it more emphasis. Wrapped in a media query to
make sure this only happens on large viewports / desktop-ish devices.

 */

@media (min-width: 701px) {
  .post-feed.list .post-card-large {
    grid-column: 1 / span 3;
    display: grid;
    grid-gap: 4vmin;
    grid-template-columns: 3fr 5fr;
    min-height: auto;
    border-top: 0;
  }

  .post-feed.list .post-card-large.no-image {
    grid-template-columns: 1fr;
  }

  .post-feed.list .post-card-large:not(.no-image) .post-card-header {
    margin-top: 0;
  }

  .post-feed.list .post-card-large .post-card-content {
    justify-content: flex-start;
    margin-top: -6px;
  }
}

@media (min-width: 1001px) {
  .post-card-large {
    grid-column: 1 / span 3;
    display: grid;
    grid-gap: 4vmin;
    grid-template-columns: 1fr 1fr 1fr;
    min-height: 280px;
    border-top: 0;
  }
  .iconos-menu {
    height: 60px;
    padding: 16px 5px 0;
  }

  .post-card-large:not(.no-image) .post-card-header {
    margin-top: 0;
  }

  .post-card-large .post-card-image-link {
    position: relative;
    grid-column: 1 / span 2;
    margin-bottom: 0;
    min-height: 380px;
  }

  .post-feed.list .post-card-large .post-card-image-link {
    grid-column: 1 / span 1;
    height: max-content;
    min-height: auto;
  }

  .post-feed.list .post-card-large .post-card-image-link::after {
    content: "";
    display: block;
    padding-bottom: 60%;
  }

  .post-card-large .post-card-image {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .post-card-large .post-card-content {
    justify-content: center;
  }

  .post-card-large .post-card-title {
    margin-top: 0;
    font-size: 3.2rem;
  }

  .post-feed.list .post-card-large .post-card-title {
    font-size: 2.4rem;
  }

  .post-card-large .post-card-excerpt p {
    font-size: 1.7rem;
    line-height: 1.55em;
    -webkit-line-clamp: 8;
  }
}

@media (max-width: 500px) {
  .post-card-title {
    font-size: 1.9rem;
  }

  .post-card-excerpt {
    font-size: 1.6rem;
  }
}

/* 7. Single Post
/* ---------------------------------------------------------- */

.article {
  padding: 8vmin 0;
  word-break: break-word;
}

.article-header {
  padding: 0 0 6vmin 0;
}

.article-tag {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0 0 0.5rem;
  color: var(--color-midgrey);
  font-size: 1.3rem;
  line-height: 1.4em;
  letter-spacing: 0.02em;
  font-weight: 600;
  text-transform: uppercase;
}

.article-tag a {
  color: var(--ghost-accent-color);
}

.article-title {
  color: #d0d7d9;
}

.has-serif-title .article-title {
  font-family: var(--font-serif);
}

.article-excerpt {
  margin: 0 0 1rem;
  font-size: 2rem;
  line-height: 1.4em;
  opacity: 0.6;
}

.gh-canvas .article-image {
  grid-column: wide-start / wide-end;
  width: 100%;
  margin: 6vmin 0 -165px;
}

.image-full .article-image {
  grid-column: full-start / full-end;
}

.image-small .article-image {
  grid-column: main-start / main-end;
}

.gh-canvas .article-image img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

@media (max-width: 600px) {
  .article-excerpt {
    font-size: 1.8rem;
  }
}

/* -------- */

/* Content grid
/* ---------------------------------------------------------- */

/* Canvas creates a multi-column, centered grid which the post
is laid out on top of. Canvas just defines the grid, we don't
use it for applying any other styles. */

.gh-canvas {
  display: grid;
  grid-template-columns:
    [full-start]
    minmax(4vmin, auto)
    [wide-start]
    minmax(auto, 240px)
    [main-start]
    min(900px, calc(100% - 8vw))
    [main-end]
    minmax(auto, 240px)
    [wide-end]
    minmax(4vmin, auto)
    [full-end];
}

.gh-canvas > * {
  grid-column: main-start / main-end;
}

.kg-width-wide {
  grid-column: wide-start / wide-end;
}

.kg-width-full {
  grid-column: full-start / full-end;
}

.kg-width-full img {
  width: 100%;
}

/* Content
/* ---------------------------------------------------------- */

/* Content refers to styling all page and post content that is
created within the Ghost editor. The main content handles
headings, text, images and lists. We deal with cards lower down. */

/* Default vertical spacing */
.gh-content > * + * {
  margin-top: 4vmin;
  margin-bottom: 0;
}

/* [id] represents all headings h1-h6, reset all margins */
.gh-content > [id] {
  margin: 0;
  color: #d0d7d9;
}

.has-serif-title .gh-content > [id] {
  font-family: var(--font-serif);
}

/* Add back a top margin to all headings, unless a heading
is the very first element in the post content */
.gh-content > [id]:not(:first-child) {
  margin: 2em 0 0;
}

/* Add a small margin between a heading and anything after it */
.gh-content > [id] + * {
  margin-top: 1.5rem !important;
}

/* A larger margin before/after HRs and blockquotes */
.gh-content > hr,
.gh-content > blockquote {
  position: relative;
  margin-top: 6vmin;
}
.gh-content > hr + *,
.gh-content > blockquote + * {
  margin-top: 6vmin !important;
}

/* Now the content typography styles */
.gh-content a {
  color: #fff;
  text-decoration: underline;
  word-break: break-word;
  font-weight: bold;
}

.gh-content > blockquote:not([class]),
.gh-content > ol,
.gh-content > ul,
.gh-content > dl,
.gh-content > p {
  font-family: var(--font-serif);
  font-weight: 400;
  font-size: 2.1rem;
  line-height: 1.6em;
}

.gh-content .kg-callout-card .kg-callout-text,
.gh-content .kg-toggle-card .kg-toggle-content > ol,
.gh-content .kg-toggle-card .kg-toggle-content > ul,
.gh-content .kg-toggle-card .kg-toggle-content > p {
  font-family: var(--font-serif);
  font-weight: 400;
  font-size: 1.9rem;
  line-height: 1.6em;
}

.gh-content .kg-product-card .kg-product-card-description > p,
.gh-content .kg-product-card .kg-product-card-description > ol,
.gh-content .kg-product-card .kg-product-card-description > ul {
  font-size: 1.7rem;
  line-height: 1.6em;
}

.gh-content .kg-callout-card .kg-callout-emoji {
  font-size: 2.1rem;
  line-height: 1.4em;
}

.gh-content .kg-toggle-card .kg-toggle-heading-text {
  font-size: 2rem;
}

.has-sans-body .gh-content > blockquote,
.has-sans-body .gh-content > ol,
.has-sans-body .gh-content > ul,
.has-sans-body .gh-content > dl,
.has-sans-body .gh-content > p,
.has-sans-body .gh-content .kg-callout-card .kg-callout-text,
.has-sans-body .gh-content .kg-toggle-card .kg-toggle-content > ol,
.has-sans-body .gh-content .kg-toggle-card .kg-toggle-content > ul,
.has-sans-body .gh-content .kg-toggle-card .kg-toggle-content > p {
  font-family: var(--font-sans);
}

.gh-content > ul,
.gh-content > ol,
.gh-content > dl,
.gh-content .kg-toggle-card .kg-toggle-content > ol,
.gh-content .kg-toggle-card .kg-toggle-content > ul,
.gh-content .kg-product-card .kg-product-card-description > ol,
.gh-content .kg-product-card .kg-product-card-description > ul {
  padding-left: 1.9em;
}

.gh-content > blockquote:not([class]) {
  position: relative;
  font-style: italic;
  padding: 0;
}

.gh-content > blockquote:not([class])::before {
  content: "";
  position: absolute;
  left: -1.5em;
  top: 0;
  bottom: 0;
  width: 0.3rem;
  background: var(--ghost-accent-color);
}

.gh-content :not(pre) > code {
  vertical-align: middle;
  padding: 0.15em 0.4em 0.15em;
  border: #e1eaef 1px solid;
  font-weight: 400 !important;
  font-size: 0.9em;
  line-height: 1em;
  color: #15171a;
  background: #f0f6f9;
  border-radius: 0.25em;
}

.gh-content pre {
  overflow: auto;
  padding: 16px 20px;
  color: var(--color-wash);
  font-size: 1.4rem;
  line-height: 1.5em;
  background: var(--color-darkgrey);
  border-radius: 5px;
  box-shadow: 0 2px 6px -2px rgba(0, 0, 0, 0.1), 0 0 1px rgba(0, 0, 0, 0.4);
}

@media (max-width: 650px) {
  .gh-content > blockquote:not([class]),
  .gh-content > ol,
  .gh-content > ul,
  .gh-content > dl,
  .gh-content > p,
  .gh-content .kg-callout-card .kg-callout-text,
  .gh-content .kg-toggle-card .kg-toggle-content > ol,
  .gh-content .kg-toggle-card .kg-toggle-content > ul,
  .gh-content .kg-toggle-card .kg-toggle-content > p {
    font-size: 1.7rem;
  }

  .gh-content .kg-product-card .kg-product-card-description > p,
  .gh-content .kg-product-card .kg-product-card-description > ol,
  .gh-content .kg-product-card .kg-product-card-description > ul {
    font-size: 1.5rem;
  }

  .gh-content blockquote:not([class])::before {
    left: -4vmin;
  }
}

/* Cards
/* ---------------------------------------------------------- */

/* Cards are dynamic blocks of content which appear within Ghost
posts, for example: embedded videos, tweets, galleries, or
specially styled bookmark links. We add extra styling here to
make sure they look good, and are given a bit of extra spacing. */

/* Add extra margin before/after any cards,
except for when immediately preceeded by a heading */
.gh-content :not(.kg-card):not([id]) + .kg-card {
  margin-top: 6vmin;
  margin-bottom: 0;
}
.gh-content .kg-card + :not(.kg-card) {
  margin-top: 6vmin;
  margin-bottom: 0;
}

/* This keeps small embeds centered */
.kg-embed-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

/* This keeps small iamges centered */
.kg-image-card img {
  margin: auto;
}

.has-serif-title .kg-toggle-card .kg-toggle-heading-text {
  font-family: var(--font-serif);
}

.gh-content .kg-callout-card-accent a {
  text-decoration: underline;
}

.kg-blockquote-alt {
  font-family: var(--font-serif);
  color: var(--color-midgrey);
}

.has-sans-body .kg-blockquote-alt {
  font-family: var(--font-sans);
}

.kg-card.kg-header-card.kg-style-dark {
  background: var(--color-darkgrey);
}

.kg-header-card.kg-style-light h2.kg-header-card-header {
  color: color-mod(var(--color-darkgrey) l(-5%));
}

.has-serif-title .kg-header-card h2.kg-header-card-header {
  font-family: var(--font-serif);
}

/* Captions */
figcaption {
  padding: 1.5rem 1.5rem 0;
  text-align: center;
  color: #d0d7d9;
  font-weight: 600;
  font-size: 1.3rem;
  line-height: 1.4em;
}
figcaption strong {
  color: rgba(0, 0, 0, 0.8);
}
figcaption a {
  color: var(--ghost-accent-color);
}

/* Highly specific styles for traditional Instagram embeds */
iframe.instagram-media {
  margin-top: 6vmin !important;
  margin-left: auto !important;
  margin-right: auto !important;
  margin-bottom: 0 !important;
}

iframe.instagram-media + script + :not([id]) {
  margin-top: 6vmin;
}

/* Card captions
/* ---------------------------------------------------------- */

.kg-width-full.kg-card-hascaption {
  display: grid;
  grid-template-columns: inherit;
}

.kg-width-wide.kg-card-hascaption img {
  grid-column: wide-start / wide-end;
}
.kg-width-full.kg-card-hascaption img {
  grid-column: 1 / -1;
}

.kg-width-full.kg-card-hascaption figcaption {
  grid-column: main-start / main-end;
}

.article-comments {
  margin: 6vmin 0 0 0;
}

/* -----old------ */

.footnotes-sep {
  margin-bottom: 30px;
}

.footnotes {
  font-size: 1.5rem;
}

.footnotes p {
  margin: 0;
}

.footnote-backref {
  font-size: 1.2rem;
  font-weight: bold;
  text-decoration: none !important;
  box-shadow: none !important;
}

/* Tables */
.post-full-content table {
  display: inline-block;
  overflow-x: auto;
  margin: 0.5em 0 2.5em;
  max-width: 100%;
  width: auto;
  border-spacing: 0;
  border-collapse: collapse;
  font-family: var(--font-sans-serif);
  font-size: 1.6rem;
  white-space: nowrap;
  vertical-align: top;
}

.post-full-content table {
  -webkit-overflow-scrolling: touch;
  background: radial-gradient(
        ellipse at left,
        rgba(0, 0, 0, 0.2) 0%,
        rgba(0, 0, 0, 0) 75%
      )
      0 center,
    radial-gradient(
        ellipse at right,
        rgba(0, 0, 0, 0.2) 0%,
        rgba(0, 0, 0, 0) 75%
      )
      100% center;
  background-attachment: scroll, scroll;
  background-size: 10px 100%, 10px 100%;
  background-repeat: no-repeat;
}

.post-full-content table td:first-child {
  background-image: linear-gradient(
    to right,
    rgba(255, 255, 255, 1) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  background-size: 20px 100%;
  background-repeat: no-repeat;
}

.post-full-content table td:last-child {
  background-image: linear-gradient(
    to left,
    rgba(255, 255, 255, 1) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  background-position: 100% 0;
  background-size: 20px 100%;
  background-repeat: no-repeat;
}

.post-full-content table th {
  color: var(--color-darkgrey);
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 0.2px;
  text-align: left;
  text-transform: uppercase;
  background-color: color-mod(var(--color-wash) l(+4%));
}

.post-full-content table th,
.post-full-content table td {
  padding: 6px 12px;
  border: color-mod(var(--color-wash) l(-1%) s(-5%)) 1px solid;
}

/* 7.1. Post Byline
/* ---------------------------------------------------------- */

.article-byline {
  display: flex;
  justify-content: space-between;
  margin: 20px 0 0;
}

.article-byline-content {
  flex-grow: 1;
  display: flex;
  align-items: center;
}

.article-byline-content .author-list {
  justify-content: flex-start;
  padding: 0 12px 0 0;
}

.article-byline-meta {
  color: color-mod(var(--color-midgrey));
  font-size: 1.4rem;
  line-height: 1.2em;
}

.article-byline-meta h4 {
  margin: 0 0 3px;
  font-size: 1.6rem;
}

.article-byline-meta .bull {
  display: inline-block;
  margin: 0 2px;
  opacity: 0.6;
}

.author-avatar {
  display: block;
  overflow: hidden;
  margin: 0 -4px;
  width: 50px;
  height: 50px;
  border: #fff 2px solid;
  border-radius: 100%;
  transition: all 0.5s cubic-bezier(0.4, 0.01, 0.165, 0.99) 700ms;
}

.basic-info .avatar-wrapper {
  position: relative;
  margin: 0;
  width: 60px;
  height: 60px;
  border: none;
  background: rgba(229, 239, 245, 0.1);
}

.basic-info .avatar-wrapper svg {
  margin: 0;
  width: 60px;
  height: 60px;
  opacity: 0.15;
}

.page-template .article-title {
  margin-bottom: 0;
}

/* 7.3. Subscribe
/* ---------------------------------------------------------- */

.footer-cta {
  position: relative;
  margin-bottom: -40px;
  padding: 9vmin 4vmin 10vmin;
  color: #fff;
  text-align: center;
  background: var(--color-darkgrey);
}

.footer-cta.cta-alt {
  padding: 2vmin 4vmin 12vmin;
  background: transparent;
}

/* Increases the default h2 size by 15%, for small and large screens */
.footer-cta h2 {
  margin: 0 0 30px;
  font-size: 3.2rem;
}

.footer-cta.cta-alt h2 {
  color: var(--color-darkgrey);
}

@media (max-width: 600px) {
  .footer-cta h2 {
    font-size: 2.65rem;
  }
}

.footer-cta-button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 500px;
  padding: 5px 5px 5px 15px;
  font-size: 1.8rem;
  color: var(--color-midgrey);
  background: #fff;
  border-radius: 8px;
}

.footer-cta.cta-alt .footer-cta-button {
  border: 1px solid var(--color-lightgrey);
}

.footer-cta-button span {
  display: inline-block;
  padding: 10px 15px;
  color: #fff;
  font-weight: 500;
  background: var(--ghost-accent-color);
  border-radius: 5px;
}

/* 7.4. Read more
/* ---------------------------------------------------------- */

.read-more-wrap {
  width: 100%;
  padding: 4vmin;
  margin: 40px auto -40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: color-mod(var(--color-darkgrey) l(-5%));
}

.read-more {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 4vmin;
}

.read-more .post-card-title {
  color: #fff;
  opacity: 0.8;
}

.read-more .post-card-excerpt {
  color: rgba(255, 255, 255, 0.6);
}

.read-more .post-card-byline-content a {
  color: #fff;
}

@media (max-width: 1000px) {
  .read-more {
    grid-template-columns: 1fr 1fr;
  }
  .read-more article:nth-child(3) {
    display: none;
  }
  .gh-canvas .article-image {
    margin-bottom: 0px;
    /* height: 350px; */
  }
}

@media (max-width: 700px) {
  .read-more {
    grid-template-columns: 1fr;
  }
  .read-more article:nth-child(2) {
    display: none;
  }
}

/* 8. Author Template
/* ---------------------------------------------------------- */

.author-template .posts {
  position: relative;
  height: 100%;
  display: grid;
  grid-template-columns: 200px 1fr 1fr;
  grid-gap: 4vmin;
}

.author-template .posts .post-feed {
  grid-column: 2 / 4;
  grid-template-columns: 1fr 1fr;
}

.author-profile {
  padding: 4vmin 0;
}

.author-profile-content {
  height: auto;
  position: sticky;
  top: 4vmin;
  font-size: 1.4rem;
  line-height: 1.4em;
}

.author-profile-pic {
  display: block;
  width: 150px;
  height: 150px;
  object-fit: cover;
  margin: 0 0 2rem;
  background: color-mod(var(--color-lightgrey) l(+10%));
  border-radius: 50%;
}

.author-profile h1 {
  margin: 0 0 0.3em;
  font-size: 2rem;
}

.author-profile p {
  margin: 0 0 1.5em;
  color: color-mod(var(--color-midgrey) l(-8%));
}

.author-profile-location {
  margin: 0 0 1.5em;
  font-weight: 700;
}

.author-profile-social-link {
  display: block;
  padding: 0 0 5px;
  font-size: 1.3rem;
  color: color-mod(var(--color-midgrey) l(-8%));
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 100%;
  white-space: nowrap;
}

.author-profile-social-link:hover {
  color: var(--color-darkgrey);
}

@media (max-width: 900px) {
  .author-template .posts .post-feed {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 650px) {
  .author-template .posts {
    grid-template-columns: 1fr;
    grid-gap: 0;
  }
  .author-template .posts .post-feed {
    grid-column: 1 / auto;
  }
  .author-profile {
    padding-right: 0;
  }
  .author-profile-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}

/* 8. Tag Template
/* ---------------------------------------------------------- */

.tag-template .post-card-large .post-card-image-link {
  grid-column: 2 / span 2;
  order: 2;
}

.tag-template .post-card-large .post-card-content {
  order: 1;
}

/* 9. Error Template
/* ---------------------------------------------------------- */

.error-content {
  padding: 14vw 4vw 6vw;
}

.error-message {
  padding-bottom: 10vw;
  text-align: center;
}

.error-code {
  margin: 0;
  color: var(--ghost-accent-color);
  font-size: 12vw;
  line-height: 1em;
  letter-spacing: -5px;
}

.error-description {
  margin: 0;
  color: var(--color-midgrey);
  font-size: 3.2rem;
  line-height: 1.3em;
  font-weight: 400;
}

.error-link {
  display: inline-block;
  margin-top: 5px;
}

@media (min-width: 940px) {
  .error-content .post-card {
    margin-bottom: 0;
    padding-bottom: 0;
    border-bottom: none;
  }
}

@media (max-width: 800px) {
  .error-content {
    padding-top: 24vw;
  }
  .error-code {
    font-size: 11.2rem;
  }
  .error-message {
    padding-bottom: 16vw;
  }
  .error-description {
    margin: 5px 0 0 0;
    font-size: 1.8rem;
  }
}

@media (max-width: 500px) {
  .error-content {
    padding-top: 28vw;
  }
  .error-message {
    padding-bottom: 14vw;
  }
}

/* 11. Site Footer
/* ---------------------------------------------------------- */

.site-footer {
  position: relative;
  /* margin: 40px 0 0 0; */
  padding: 40px 0px 0px 0px;
  color: #d0d7d9;
  background-color: rgb(255, 255, 255);
}

.site-footer .inner {
  display: grid;
  grid-gap: 40px;
  grid-template-columns: auto 1fr auto;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.3rem;
}

.site-footer .copyright a {
  color: #d0d7d9;
  letter-spacing: -0.015em;
  font-weight: 500;
}

.site-footer a {
  color: #7a3d71;
  border-bottom: 2px solid#fff;
}

.site-footer a:hover {
  /*color: rgba(255, 255, 255, 1);*/
  text-decoration: none;
}

.site-footer-nav ul {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 0 20px;
  padding: 0;
  list-style: none;
}

.site-footer-nav li {
  display: inline-flex;
  align-items: center;
  padding: 0;
  margin: 0;
  line-height: 2em;
}

.site-footer-nav a {
  position: relative;
  display: inline-flex;
  align-items: center;
  margin-left: 10px;
}

.site-footer-nav li:not(:first-child) a:before {
  content: "";
  display: block;
  width: 2px;
  height: 2px;
  margin: 0 10px 0 0;
  background: #d0d7d9;
  border-radius: 100%;
}

@media (max-width: 800px) {
  .site-footer .inner {
    max-width: 500px;
    grid-template-columns: 1fr;
    grid-gap: 0;
    text-align: center;
  }
  .site-footer .copyright,
  .site-footer .copyright a {
    color: #d0d7d9;
    font-size: 1.5rem;
  }
}

/* 12. Dark Mode
/* ---------------------------------------------------------- */

/* If you prefer a dark color scheme, you can enable dark mode
by adding the following code to the Head section of "Code Injection"
settings inside: Ghost Admin > Settings > Advanced

<script>document.documentElement.classList.add('dark-mode');</script>site-foo

Or you can just edit default.hbs and add the .dark-mode class directly
to the html tag on the very first line of the file.

 */

html.dark-mode body {
  color: rgba(255, 255, 255, 0.75);
  background: var(--color-darkmode);
}

html.dark-mode img {
  opacity: 0.9;
}

html.dark-mode kbd {
  background: color-mod(var(--color-darkmode) l(+5%));
}

html.dark-mode .post-card,
html.dark-mode .post-card:hover {
  border-bottom-color: color-mod(var(--color-darkmode) l(+8%));
}

html.dark-mode .post-card-byline-content a {
  color: rgba(255, 255, 255, 0.75);
}

html.dark-mode .post-card-byline-content a:hover {
  color: #fff;
}

html.dark-mode .post-card-image {
  background: var(--color-darkmode);
}

html.dark-mode .post-card-title {
  color: rgba(255, 255, 255, 0.85);
}

html.dark-mode .post-card-excerpt {
  color: color-mod(var(--color-midgrey) l(+10%));
}

html.dark-mode .post-full-content {
  background: var(--color-darkmode);
}

html.dark-mode .article-title {
  color: rgba(255, 255, 255, 0.9);
}

html.dark-mode .article-excerpt {
  color: color-mod(var(--color-midgrey) l(+10%));
}

html.dark-mode .post-full-image {
  background-color: color-mod(var(--color-darkmode) l(+8%));
}

html.dark-mode .article-byline {
  border-top-color: color-mod(var(--color-darkmode) l(+15%));
}

html.dark-mode .article-byline-meta h4 a {
  color: rgba(255, 255, 255, 0.75);
}

html.dark-mode .article-byline-meta h4 a:hover {
  color: #fff;
}

html.dark-mode .no-image .author-social-link a {
  color: rgba(255, 255, 255, 0.75);
}

html.dark-mode .gh-content > [id] {
  color: rgba(255, 255, 255, 0.9);
}

html.dark-mode .gh-content pre {
  background: color-mod(var(--color-darkgrey) l(-8%));
}

html.dark-mode .gh-content :not(pre) > code {
  background: color-mod(var(--color-darkgrey) l(+6%));
  border-color: color-mod(var(--color-darkmode) l(+8%));
  color: var(--color-wash);
}

html.dark-mode
  .gh-content
  a:not(.kg-btn):not(.kg-nft-card-container):not(.kg-product-card-button):not(
    .kg-header-card-button
  ) {
  color: #fff;
}

html.dark-mode .gh-content strong {
  color: #fff;
}

html.dark-mode .gh-content em {
  color: #fff;
}

html.dark-mode .gh-content code {
  color: #fff;
  background: #000;
}

html.dark-mode hr {
  border-top-color: color-mod(var(--color-darkmode) l(+8%));
}

html.dark-mode .gh-content hr:after {
  background: color-mod(var(--color-darkmode) l(+8%));
  box-shadow: var(--color-darkmode) 0 0 0 5px;
}

html.dark-mode figcaption {
  color: rgba(255, 255, 255, 0.6);
}

html.dark-mode .gh-content table td:first-child {
  background-image: linear-gradient(
    to right,
    var(--color-darkmode) 50%,
    color-mod(var(--color-darkmode) a(0%)) 100%
  );
}

html.dark-mode .gh-content table td:last-child {
  background-image: linear-gradient(
    to left,
    var(--color-darkmode) 50%,
    color-mod(var(--color-darkmode) a(0%)) 100%
  );
}

html.dark-mode .gh-content table th {
  color: rgba(255, 255, 255, 0.85);
  background-color: color-mod(var(--color-darkmode) l(+8%));
}

html.dark-mode .gh-content table th,
html.dark-mode .gh-content table td {
  border: color-mod(var(--color-darkmode) l(+8%)) 1px solid;
}

html.dark-mode .gh-content input {
  color: color-mod(var(--color-midgrey) l(-30%));
}

html.dark-mode .site-archive-header .no-image {
  color: rgba(255, 255, 255, 0.9);
  background: var(--color-darkmode);
}

html.dark-mode .kg-header-card.kg-style-dark {
  background: color-mod(var(--color-darkgrey) l(-5%));
}

html.dark-mode .kg-header-card.kg-style-light {
  background: color-mod(var(--color-darkgrey) l(+5%));
}

html.dark-mode .kg-header-card h2.kg-header-card-header,
html.dark-mode .kg-header-card h3.kg-header-card-subheader {
  color: #fff;
}

@media (prefers-color-scheme: dark) {
  html.auto-color body {
    color: rgba(255, 255, 255, 0.75);
    background: var(--color-darkmode);
  }

  html.auto-color img {
    opacity: 0.9;
  }

  html.auto-color kbd {
    background: color-mod(var(--color-darkmode) l(+5%));
  }

  html.auto-color .post-card,
  html.auto-color .post-card:hover {
    border-bottom-color: color-mod(var(--color-darkmode) l(+8%));
  }

  html.auto-color .post-card-byline-content a {
    color: rgba(255, 255, 255, 0.75);
  }

  html.auto-color .post-card-byline-content a:hover {
    color: #fff;
  }

  html.auto-color .post-card-image {
    background: var(--color-darkmode);
  }

  html.auto-color .post-card-title {
    color: rgba(255, 255, 255, 0.85);
  }

  html.auto-color .post-card-excerpt {
    color: color-mod(var(--color-midgrey) l(+10%));
  }

  html.auto-color .post-full-content {
    background: var(--color-darkmode);
  }

  html.auto-color .article-title {
    color: rgba(255, 255, 255, 0.9);
  }

  html.auto-color .article-excerpt {
    color: color-mod(var(--color-midgrey) l(+10%));
  }

  html.auto-color .post-full-image {
    background-color: color-mod(var(--color-darkmode) l(+8%));
  }

  html.auto-color .article-byline {
    border-top-color: color-mod(var(--color-darkmode) l(+15%));
  }

  html.auto-color .article-byline-meta h4 a {
    color: rgba(255, 255, 255, 0.75);
  }

  html.auto-color .article-byline-meta h4 a:hover {
    color: #fff;
  }

  html.auto-color .no-image .author-social-link a {
    color: rgba(255, 255, 255, 0.75);
  }

  html.auto-color .gh-content > [id] {
    color: rgba(255, 255, 255, 0.9);
  }

  html.auto-color .gh-content pre {
    background: color-mod(var(--color-darkgrey) l(-8%));
  }

  html.auto-color .gh-content :not(pre) > code {
    background: color-mod(var(--color-darkgrey) l(+6%));
    border-color: color-mod(var(--color-darkmode) l(+8%));
    color: var(--color-wash);
  }

  html.auto-color
    .gh-content
    a:not(.kg-btn):not(.kg-nft-card-container):not(.kg-product-card-button):not(
      .kg-header-card-button
    ) {
    color: #fff;
  }

  html.auto-color .gh-content strong {
    color: #fff;
  }

  html.auto-color .gh-content em {
    color: #fff;
  }

  html.auto-color .gh-content code {
    color: #fff;
    background: #000;
  }

  html.auto-color hr {
    border-top-color: color-mod(var(--color-darkmode) l(+8%));
  }

  html.auto-color .gh-content hr:after {
    background: color-mod(var(--color-darkmode) l(+8%));
    box-shadow: var(--color-darkmode) 0 0 0 5px;
  }

  html.auto-color figcaption {
    color: rgba(255, 255, 255, 0.6);
  }

  html.auto-color .gh-content table td:first-child {
    background-image: linear-gradient(
      to right,
      var(--color-darkmode) 50%,
      color-mod(var(--color-darkmode) a(0%)) 100%
    );
  }

  html.auto-color .gh-content table td:last-child {
    background-image: linear-gradient(
      to left,
      var(--color-darkmode) 50%,
      color-mod(var(--color-darkmode) a(0%)) 100%
    );
  }

  html.auto-color .gh-content table th {
    color: rgba(255, 255, 255, 0.85);
    background-color: color-mod(var(--color-darkmode) l(+8%));
  }

  html.auto-color .gh-content table th,
  html.auto-color .gh-content table td {
    border: color-mod(var(--color-darkmode) l(+8%)) 1px solid;
  }

  html.auto-color .gh-content input {
    color: color-mod(var(--color-midgrey) l(-30%));
  }

  html.auto-color .site-archive-header .no-image {
    color: rgba(255, 255, 255, 0.9);
    background: var(--color-darkmode);
  }

  html.auto-color .kg-header-card.kg-style-dark {
    background: color-mod(var(--color-darkgrey) l(-5%));
  }

  html.auto-color .kg-header-card.kg-style-light {
    background: color-mod(var(--color-darkgrey) l(+5%));
  }

  html.auto-color .kg-header-card h2.kg-header-card-header,
  html.auto-color .kg-header-card h3.kg-header-card-subheader {
    color: #fff;
  }
}

