/* More Button / Dropdown Menu */

.more-btn,
.more-menu-btn {
  background: none;
  border: 0 none;
  line-height: normal;
  overflow: visible;
  /* -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none; */
  /* width: 100%; */
  /* text-align: left; */
  outline: none;
  cursor: pointer;
}

.more-dot {
  background-color: #0081b3;
  margin: 0 auto;
  display: inline-block;
  width: 6px;
  height: 6px;
  margin-right: 1px;
  border-radius: 50%;
  transition: background-color 0.3s;
}

.more-menu {
  position: absolute;
  top: 70%;
  z-index: 900;
  /* float: left; */
  padding: 5px;
  /* margin-top: -5px; */
  background-color: #0d3b49;
  /* border: 0px; */
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 5px 5px -3px,
    rgba(0, 0, 0, 0.14) 0px 8px 10px 1px, rgba(0, 0, 0, 0.12) 0px 3px 14px 2px;
  opacity: 0;
  transform: translate(0, 15px) scale(0.95);
  transition: transform 0.1s ease-out, opacity 0.1s ease-out;
  pointer-events: none;
  width: 100%;
  /* margin: 50px; */
  max-width: calc(100% - 20px);
  /* max-height: calc(100% - 32px); */
  outline: currentcolor none 0px;
}

.more-menu-items {
  margin: 0;
  list-style: none;
  padding: 0;
}

.more-menu-item {
  display: block;
}

.more-menu-btn {
  min-width: 100%;
  color: #666666;
  cursor: pointer;
  display: block;
  /* font-size: 13px; */
  line-height: 18px;
  padding: 5px 10px;
  position: relative;
  white-space: nowrap;

  font-size: 1.5rem;
  box-sizing: border-box;
  /* min-height: 48px; */
  font-family: Noto Sans SC, "Helvetica", "Arial", "sans-serif";
  font-weight: 400;
  line-height: 1.75;
}

.more-menu-item:hover {
  background-color: #f2f2f2;
}
.more-menu-item a:hover {
  background-color: #f2f2f2;
  text-decoration: none;
}

.more-menu-item:hover .more-menu-btn {
  color: #666666;
}

.more-btn:hover .more-dot,
.show-more-menu .more-dot {
  background-color: #516471;
}

.show-more-menu .more-menu {
  opacity: 1;
  transform: translate(0, 0) scale(1);
  pointer-events: auto;
}

.footer-link:hover {
  /*color: #fff !important;*/
  border-bottom: 2px solid #3ab0e5;
  text-decoration: none;
}

.dropdown-menu {
